// Base
*,::before, ::after{
    box-sizing: border-box;
}

body{
    position: relative;
    margin: 0 0 0 0;
    padding: 0 1rem;
    //font-family: $fuente_principal;
    transition: .5s;
}

a{
    text-decoration: none;
}

.header{
    width: 100%;
    //height: $header-height;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 1rem;
    //background-color: $white-color;
    //z-index: $z-fixed;
    transition: .5s;
}

.header__toogle{
    //color: $first-color;
    font-size: 1.5rem;
    cursor: pointer;
}

.header__img{
    width: 35px;
    height: 35px;
    display: flex;
    justify-content: center;
    border-radius: 50%;
    overflow: hidden;
}

.header__img img{
    width: 40px;
}

/*=========NAV=========*/

.l-navbar{
    position: fixed;
    top: 0;
    left: 0;
}